@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@300&display=swap");

.App {
  text-align: center;
  background-color: lightblue;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

body {
  font-family: "Noto Sans", sans-serif;
}

#quote-box {
  display: flex;
  flex-direction: column;
  border-radius: 3px;
  margin:auto;
  width: 600px;
  padding: 50px;
  background-color: #282c34;
}
#text {
  font-size: 20px;
  text-align: center;
  padding: 40px 50px 0px 50px;
}

#author {
  text-align: right;
  padding: 10px 50px;
}

#new-quote {
  border: green;
  float: right;
  /* color: white; */
  border-radius: 10px;
  margin-right: 40px;

}
#buttons {
  padding: 10px;
}